.navbar {
    background: #303030;
    width: 100%;
    max-width: 85rem;
    height: 5rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo {
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktopMenu {
    display: flex;
    list-style: none;
}

.desktopMenuListItem {
    margin: 1rem;
    cursor: pointer;
    color: white;
}

.desktopMenuListItem:hover {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid yellow;
}

.desktopBtn {
    background: white;
    padding: 15px 20px;
    border: none;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    text-transform: capitalize;
}

.desktopMenuImg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
}

.active {
    color: yellow;
    padding-bottom: .5rem;
    border-bottom: 3px solid yellow;
}

.mobMenu {
    display: none;
    object-fit: cover;
    height: 1.8rem;
}

.navMenu {
    position: absolute;
    top: 4.2rem;
    right: 1rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    height: fit-content;
    min-width: 15rem;
    background: #303030;
    border-bottom: 2px solid yellow;
    border-radius: 1rem;
}

.ListItem {
    color: white;
    padding: .8rem 3rem;
    margin: 0.5rem;
    border-radius: 8px;
    background: #666;
}

@media (max-width:720px) {
    .mobMenu {
        display: flex;
    }

    .desktopMenu {
        display: none;
    }

    .desktopBtn {
        display: none;
    }
}