.intro {
    height: calc(100vh - 5rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg {
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}

.intro-content {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-weight: 700;
}

.hello {
    font-size: 1.75rem;
    font-weight: 100;
    color: gray;
}

.introName {
    color: yellow;
}

.intropara {
    font-weight: 300;
    letter-spacing: 1px;
    font-size: medium;
    color: lightgray;
    margin-top: 20px;
}

.btn {
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.btn .btnImg {
    object-fit: cover;
    margin: 0;
    margin-right: .7rem;
    height: .75rem;
}

.hireme {
    display: flex;
    align-items: center;
}

.btn:hover {
    transition: .7s all;
    background: yellow;
}

@media (max-width:840px) {
    .bg {
        right: -5vw;
    }

    .intro-content {
        font-size: 10vw;
    }

    .hello {
        font-size: 4.5vw;
    }
}

@media (max-width:480px) {
    .bg {
        right: -22vw;
    }
}

@media (max-width:400px) {
    .bg {
        right: -14vw;
    }

    .intro-content {
        margin-top: 1.5rem;
    }
}