#works {
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0 1rem;
    justify-content: center;
}

.worksTitle {
    margin: 1.2rem 0;
    text-transform: capitalize;
    font-size: 3rem;
}

.worksTitle span {
    color: yellow;
}

.worksDesc {
    font-weight: 300;
    font-size: 1rem;
    color: lightgray;
    max-width: 45rem;
    line-height: 22px;
}

.worksImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}

.workImg {
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}

#works button { 
    margin: 2rem;
    padding: .7rem 2.5rem;
    background: white;
    border: none;
    border-radius: 50px;
}
#works button:hover{
    transition: .7s all;
    background: yellow;
}

@media (max-width :585px) {
    .workImg{
        height: 48vw;
    }
}