#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle {
    padding-top: 3rem;
    font-size: 3rem;
    font-weight: 600;
    margin-bottom: 1.5rem;
}

.skillTitle span {
    color: yellow;
}

.skilldesc {
    font-weight: 300;
    font-size: 1rem;
    color: lightgrey;
    max-width: 51rem;
    padding: 0 2rem;
    line-height: 23px;
}

.skillBars {
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}

.skillBar {
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 8px;
    background: #333;
}

.skillBar:hover {
    box-shadow: inset 0 0 20px -5px rgb(255, 255, 185);
}

.skillBar img {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}

.skillBarText>p {
    font-size: .85rem;
    font-weight: 200;
    color: lightgray;
}

@media (max-width:720px) {
    .skillBarText>p {
        font-size: 2vw;
    }
}

@media (max-width:480px) {
    .skillBarText>p {
        font-size: 3vw;
    }

    .skillBarImg {
        height: 2.25rem;
        width: 2.25rem;
    }

    .skillBarText h2 {
        font-size: 5vw;
    }
}