#contactPage {
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.contactPageTitle span {
    color: yellow;
}

.clientDesc {
    font-weight: 300;
    font-size: 1rem;
    color: lightgray;
    line-height: 22px;
}

.clientImgs {
    display: flex;
    margin: 1rem 0;
    flex-wrap: wrap;
}

.clientImg {
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    margin: auto;
    transition: .5s all;
    padding: 0.25rem;
}

.clientImg:hover {
    scale: 1.05;
}

#client {
    padding: 2rem;
}

.contactDesc {
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
    color: lightgray;
}

.contactForm {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}

.name,
.email,
.msg {
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: .8rem 1rem;
    color: white;
    border: none;
    border-radius: .5rem;
    background: #444;
    resize: vertical;
    outline: none;
}

.name:focus,
.email:focus,
.msg:focus {
    border: 2px solid yellow;
}

textarea {
    width: 100%;
    max-width: 40rem;
    height: 10rem;
}

.submitBtn {
    background: white;
    padding: .75rem 4rem;
    border: none;
    transition: .6s all;
    border-radius: 50px;
}

.links {
    display: flex;
    flex-wrap: wrap;

}

.link {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 2rem .75rem;
}